  methods: {
    clickNewTask: function () {
      this.taskEdit = null
      var moment = require('moment');
      let taskNew = {
        id: '',
        name: 'New title',
        description: "",
        dateCreated: moment().format('DD.MM.yyyy HH:mm:ss'),
        dateConfession: ""
      }
      this.taskEdit = taskNew
    },
    clickActiveTask: function (index) {
      this.taskEdit = JSON.parse(JSON.stringify(this.tasks[index]))
    },
    updateTasks: function () {
      axios.get('http://localhost:8082/getAllTasks',)
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
    },
    deleteTask: function (id) {
      console.log(id)
      axios.get('http://localhost:8082/deleteIdTasks/' + id,)
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
      setTimeout(this.updateTasks, 100)
    },

    clickGenre: function (genre) {
      this.activeGenre = genre
    },
    plusGenre: function () {
      this.editGenre = null
      let editNew = {
        id: '',
        name: 'Title',
        type: ""
      }
      this.editGenre = editNew
      this.modalType = 'genrePlus'
      this.modalActive = true

    },
    minusGenre: function () {
      this.editGenre = null
      let editNew = {
        id: '',
        name: 'Choose...',
        type: ""
      }
      this.editGenre = editNew
      this.modalType = 'genreMinus'
      this.modalActive = true
    },
    plusFilm: function () {
      this.editFilm = null
      let editNew = {
        id: '',
        title: 'Title...',
        description: "Text description..",
        genre: [{name: "Все", type: "all"}]
      }
      this.editFilm = editNew
      this.modalType = 'filmPlus'
      this.modalActive = true
    },
    addGenreServer: function () {
      const data = this.editGenre
      axios.post('http://localhost:8082/editGenreJson', data, {
        header: {
          'Accept': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Credentials': 'true',
          'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS,POST,PUT',
          'Access-Control-Allow-Headers': 'Access-Control-Allow-Headers, Origin,Accept, X-Requested-With, Content-Type, Access-Control-Request-Method, Access-Control-Request-Headers',
          'Content-Type': 'application/json',
        }
      })
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
      this.modalActive = false
      setTimeout(this.updateGenres, 100)
    },
    addFilmServer: function () {
      const data = this.editFilm
      axios.post('http://localhost:8082/addFilmJson', data, {
        header: {
          'Accept': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Credentials': 'true',
          'Access-Control-Allow-Methods': 'GET,HEAD,OPTIONS,POST,PUT',
          'Access-Control-Allow-Headers': 'Access-Control-Allow-Headers, Origin,Accept, X-Requested-With, Content-Type, Access-Control-Request-Method, Access-Control-Request-Headers',
          'Content-Type': 'application/json',
        }
      })
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
      this.modalActive = false
      setTimeout(this.updateFilms, 100)
    },
    delGenreServer: function () {
      axios.get('http://localhost:8082/deleteIdGenre/' + this.editGenre.id,)
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
      setTimeout(this.updateGenres, 100)
      this.modalActive = false

      this.modalActive = false
    },
    delFilmServer:function () {
      axios.get('http://localhost:8082/deleteIdFilm/' + this.editFilm.id,)
          .then(response => (this.tasks = response.data))
          .catch(function (error) {
            console.log(error)
          })
      setTimeout(this.updateFilms, 100)
      this.modalActive = false
    },
    updateGenres: function () {
      axios.get('http://localhost:8082/getAllGenres',)
          .then(response => (this.genres = response.data))
          .catch(function (error) {
            console.log(error)
          })
    },
    updateFilms: function () {
      axios.get('http://localhost:8082/getAllFilms',)
          .then(response => (this.films = response.data))
          .catch(function (error) {
            console.log(error)
          })
    },
    compare: function (mas, value) {
      var flag = -1
      for (var i = 0; i < mas.length; i++) {
        if (mas[i].type == value) {
          flag = 0
        }
      }
      return flag;
    },
    inputSelect: function () {
      if (this.compare(this.editFilm.genre, this.editGenre.type) !== 0)
        this.editFilm.genre[this.editFilm.genre.length] = {id:"", name: this.editGenre.name, type: this.editGenre.type}
    },
    delGenreAddFilm:function (index) {
      this.editFilm.genre.splice(index,1)
      console.log(index)
    },
    filmChange:function (index) {
      this.editFilm = null
      this.editFilm = JSON.parse(JSON.stringify(this.films[index]))
      this.modalType = 'filmChange'
      this.modalActive = true
    }
  },
  comments: {
