import Vue from 'vue'
import BootstrapVue from "bootstrap-vue";
import App from './App.vue'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap-vue/dist/bootstrap-vue.css"

import { library } from '@fortawesome/fontawesome-svg-core'
// import { faTwitter } from '@fortawesome/free-brands-svg-icons'
import {faMinus, faPaintRoller, faPencilAlt, faPlus, faTrashAlt} from '@fortawesome/free-solid-svg-icons'
// import { faUser } from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import locale from 'element-ui/lib/locale/lang/ru-RU'



library.add(faPencilAlt, faTrashAlt, faPlus, faMinus, faTrashAlt, faPaintRoller);

Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.use(BootstrapVue)
Vue.use(ElementUI, {locale, size: 'small', format:'dd.MM.yyyy HH:mm:ss'});

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
